import React, { Fragment } from "react";
import cx from "clsx";
import { makeStyles } from "@material-ui/core/styles";
import Card from "@material-ui/core/Card";
import CardMedia from "@material-ui/core/CardMedia";
import CardContent from "@material-ui/core/CardContent";
import CardActions from "@material-ui/core/CardActions";
import Button from "@material-ui/core/Button";
import Dialog from "@material-ui/core/Dialog";
import DialogTitle from "@material-ui/core/DialogTitle";
import DialogContent from "@material-ui/core/DialogContent";
import DialogActions from "@material-ui/core/DialogActions";
import Typography from "@material-ui/core/Typography";
import Typewriter from "typewriter-effect";
import TextInfoContent from "@mui-treasury/components/content/textInfo";
import { useFourThreeCardMediaStyles } from "@mui-treasury/styles/cardMedia/fourThree";
import { useN04TextInfoContentStyles } from "@mui-treasury/styles/textInfoContent/n04";

const useStyles = makeStyles(() => ({
  root: {
    maxWidth: 343,
    margin: "auto",
    borderRadius: 12,
    padding: 12,
    backgroundColor: "#D7DBDD",
  },
  media: {
    borderRadius: 6,
  },
  dialogContainer: {
    backgroundColor: "#ECF0F1",
    "&::-webkit-scrollbar-track": {
      backgroundColor: "#ECF0F1",
    },
    "&::-webkit-scrollbar": {
      width: "12px",
      backgroundColor: "#ECF0F1",
    },

    "&::-webkit-scrollbar-thumb": {
      borderRadius: "10px",
      backgroundColor: "#555",
    },
  },
}));

export const MusicCardDemo = React.memo(function MusicCard() {
  const styles = useStyles();
  const mediaStyles = useFourThreeCardMediaStyles();
  const textCardContentStyles = useN04TextInfoContentStyles();

  const [open, setOpen] = React.useState(false);

  const handleClickOpen = () => {
    setOpen(true);
  };
  const handleClose = () => {
    setOpen(false);
  };

  return (
    <Fragment>
      <Card className={cx(styles.root)} elevation={2}>
        <CardMedia
          className={cx(styles.media, mediaStyles.root)}
          image={
            "https://c7.uihere.com/files/6/762/424/abstract-with-low-poly-geometric.jpg"
          }
        />
        <CardContent>
          <TextInfoContent
            classes={textCardContentStyles}
            overline={"Coming Soon"}
            heading={"Coming Soon"}
          />
        </CardContent>
        <CardActions
          disableSpacing
          style={{ display: "flex", justifyContent: "center" }}
        >
          <Button
            aria-label="readmore"
            style={{ marginRight: "10px",fontWeight: 600 }}
            onClick={handleClickOpen}
          >
            About
          </Button>
          <Button
            aria-label="demo"
            href="https://www.google.com"
            target="__blank"
            style={{fontWeight: 600 }}
          >
            Demo
          </Button>
          <Button
            aria-label="demo"
            href="https://www.google.com"
            target="__blank"
            style={{fontWeight: 600 }}
          >
            Code
          </Button>
        </CardActions>
      </Card>
      <Dialog
        onClose={handleClose}
        aria-labelledby="customized-dialog-title"
        open={open}
      >
        <DialogTitle
          id="customized-dialog-title"
          onClose={handleClose}
          className={styles.dialogContainer}
        >
          <Typewriter
            options={{
              strings: ["Project Title"],
              autoStart: true,
              loop: true,
              cursor: "",
            }}
          />
        </DialogTitle>
        <DialogContent dividers className={styles.dialogContainer}>
          <Typography gutterBottom variant="h6">
            Project Description 1 Lorem ipsum is a pseudo-Latin text used in web
            design, typography, layout, and printing in place of English to
            emphasise design elements over content. It's also called placeholder
            (or filler) text. It's a convenient tool for mock-ups. It helps to
            outline the visual elements of a document or presentation, eg
            typography, font, or layout.
          </Typography>
          <Typography gutterBottom variant="h6">
            Project Description 2 Lorem ipsum is a pseudo-Latin text used in web
            design, typography, layout, and printing in place of English to
            emphasise design elements over content. It's also called placeholder
            (or filler) text. It's a convenient tool for mock-ups. It helps to
            outline the visual elements of a document or presentation, eg
            typography, font, or layout.
          </Typography>
          <Typography gutterBottom variant="h6">
            Project Description 3 Lorem ipsum is a pseudo-Latin text used in web
            design, typography, layout, and printing in place of English to
            emphasise design elements over content. It's also called placeholder
            (or filler) text. It's a convenient tool for mock-ups. It helps to
            outline the visual elements of a document or presentation, eg
            typography, font, or layout.
          </Typography>
        </DialogContent>
        <DialogActions className={styles.dialogContainer}>
          <Button autoFocus onClick={handleClose} color="primary">
            Okay
          </Button>
        </DialogActions>
      </Dialog>
    </Fragment>
  );
});
export default MusicCardDemo;
